.drop {
  position: relative;
  margin-bottom: 15px;
}

.drop__wrap:hover .drop__box {
  visibility: visible;
  transition: all 0.3s ease;
}

.drop__btn {
  min-height: 40px;
  width: 100%;
  background-color: #ffffff;
  padding: 10px 20px;
  border-radius: 22px;
  border: 1px solid #e5e5e5;
  text-align: left;
  color: #656565;
}

.drop__btn i {
  padding-right: 10px;
  font-weight: 900;
}

.drop__box {
  position: absolute;
  top: calc(100% + 15px);
  width: 100%;
  max-height: 330px;
  visibility: hidden;
  border-radius: 22px;
  background-color: #ffffff;
  border: 1px solid #e5e5e5;
  overflow-y: auto;
  transition: all 0.3s ease;
  left: 0;
}

@media (min-width: 768px) {
  .drop__box {
    right: auto;
    left: 0;
  }
}

.drop__box li:hover {
  background-color: #ecf1f8;
  border-radius: 22px;
}

.drop__box a {
  display: block;
  padding: 10px 20px;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #656565;
}

.drop__box span {
  margin-left: 10px;
}

.output-drop {
  position: relative;
}

.output-drop__wrap:hover .output-drop__box {
  visibility: visible;
  transition: all 0.3s ease;
}

.output-drop__btn {
  width: 100%;
  background-color: #f2f2f2;
  padding: 8px 14px;
  border-radius: 24px;
  color: #656565;
  min-width: 148px;
}

.output-drop__btn i {
  font-weight: 900;
}

.output-drop__box {
  position: absolute;
  top: calc(100% + 10px);
  width: 100%;
  border-radius: 24px;
  min-width: 148px;
  visibility: hidden;
  background-color: #f2f2f2;
  transition: all 0.3s ease;
  z-index: 1;
}

.output-drop__box li {
  display: block;
  padding: 14px;
  font-weight: 500;
  color: #656565;
}

.output-drop__box li:hover {
  background-color: #ffffff;
  border-radius: 22px;
}

.sorting {
  padding-right: 44px;
}

.output {
  padding: 0 10px;
}

.all-select {
  background-color: #f2f2f2;
  padding: 7px 14px;
  border-radius: 24px;
  color: #656565;
}

.card {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.06);
  border: 1px solid #f2f2f2;
  padding: 30px 14px 22px;
  align-items: center;
  text-align: center;
  width: 100%;
  color: #656565;
  position: relative;
}

.card__stickers {
  position: absolute;
  top: 14px;
  left: 0;
}

.card__actions {
  display: flex;
  margin: 0 -4px;
  position: absolute;
  top: 14px;
  right: 14px;
}

.card__actions li {
  padding: 0 4px;
}

.card__img {
  height: 230px;
  width: 240px;
  margin-bottom: 14px;
  display: flex;
  align-items: center;
}

.card__img img {
  display: block;
  width: 100%;
}

.card__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 12px;
}

.card__text {
  font-size: 14px;
  line-height: 16px;
  margin-bottom: 18px;
}

.card__box {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.card__price {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 12px;
}

.card__price s {
  color: #b1b1b1;
}

.card__btn {
  margin-top: auto;
}

.card__btn .buy-btn {
  margin-bottom: 10px;
}

@media (min-width: 1200px) {
  .card.card--middle {
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    padding: 20px 30px 0;
    font-size: 12px;
  }
  .card.card--middle .card__img {
    flex-shrink: 0;
  }
  .card.card--middle .card__info {
    justify-content: flex-start;
    margin-bottom: 16px;
  }
  .card.card--middle .card__code {
    padding-right: 28px;
  }
  .card.card--middle .card__content {
    padding-left: 30px;
    text-align: left;
  }
  .card.card--middle .card__text {
    font-size: 18px;
    line-height: 21px;
  }
  .card.card--middle .card__text::before {
    content: "";
    display: block;
    width: 550px;
    border-bottom: 1px solid #f2f2f2;
    margin-bottom: 10px;
  }
  .card.card--middle .card__box {
    display: block;
  }
  .card.card--middle .card__price {
    margin-bottom: 14px;
  }
  .card.card--middle .card__btn {
    display: flex;
    align-items: center;
  }
  .card.card--middle .card__btn .buy-btn {
    margin-right: 10px;
    margin-bottom: 0;
  }
}

@media (min-width: 1200px) {
  .card.card--large {
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    padding: 20px 30px;
  }
  .card.card--large .card__img {
    width: 100px;
    height: 100px;
    flex-shrink: 0;
  }
  .card.card--large .card__info {
    justify-content: flex-start;
    margin-bottom: 10px;
  }
  .card.card--large .card__code {
    padding-right: 28px;
  }
  .card.card--large .card__content {
    padding-left: 30px;
    text-align: left;
  }
  .card.card--large .card__text {
    font-size: 16px;
    line-height: 22px;
  }
  .card.card--large .card__text::before {
    content: "";
    display: block;
    width: 100%;
    border-bottom: 1px solid #f2f2f2;
    margin-bottom: 10px;
  }
  .card.card--large .card__box {
    display: flex;
    align-items: center;
    flex-direction: row;
  }
  .card.card--large .card__price {
    margin-bottom: 0;
    padding-right: 18px;
  }
  .card.card--large .card__btn {
    display: flex;
    align-items: center;
  }
  .card.card--large .card__btn .buy-btn {
    margin-right: 10px;
    margin-bottom: 0;
  }
}

