@font-face {
  font-family: "Montserrat";
  src: url("/blog/wp-content/themes/apd/fonts/montserrat/Montserrat-Medium.eot");
  src: url("/blog/wp-content/themes/apd/fonts/montserrat/Montserrat-Medium.eot?#iefix") format("embedded-opentype"), url("/blog/wp-content/themes/apd/fonts/montserrat/Montserrat-Medium.woff") format("woff"), url("/blog/wp-content/themes/apd/fonts/montserrat/Montserrat-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Montserrat";
  src: url("/blog/wp-content/themes/apd/fonts/montserrat/Montserrat-Bold.eot");
  src: url("/blog/wp-content/themes/apd/fonts/montserrat/Montserrat-Bold.eot?#iefix") format("embedded-opentype"), url("/blog/wp-content/themes/apd/fonts/montserrat/Montserrat-Bold.woff") format("woff"), url("/blog/wp-content/themes/apd/fonts/montserrat/Montserrat-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

.header-top {
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .header-top {
    height: 96px;
  }
}

.header-top__img {
  max-width: 100px;
}

@media (min-width: 768px) {
  .header-top__img {
    max-width: 266px;
  }
}

.list {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.list button {
  color: #ffffff;
  font-size: 14px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

@media (min-width: 768px) {
  .list button {
    font-size: 18px;
  }
}

.list__item {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  background-color: #005780;
  position: relative;
  display: flex;
  flex-shrink: 0;
  transition: 0.3s;
}

@media (min-width: 768px) {
  .list__item {
    height: 40px;
    width: 40px;
  }
}

.list__item::before {
  display: inline-block;
  content: "0";
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: #e9445df6;
  color: #ffffff;
  font-size: 10px;
  text-align: center;
  line-height: 14px;
  position: absolute;
  top: -10%;
  right: -10%;
}

@media (min-width: 768px) {
  .list__item::before {
    height: 14px;
    width: 14px;
    top: -4%;
    right: -4%;
  }
}

.list__item:hover {
  transform: scale(1.2);
}

.background {
  height: 58px;
  background-color: #005780;
}

.catalog {
  display: none;
}

@media (min-width: 1024px) {
  .catalog {
    display: inline-block;
  }
}

.header-bottom__catalog {
  text-align: center;
  background-color: #e9445df6;
  padding: 18px 0 17px 20px;
}

.header-bottom__catalog a {
  color: #ffffff;
  font-size: 20px;
  line-height: 23px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 2px;
}

.header-bottom__catalog a i {
  font-weight: 900;
  padding-right: 20px;
}

.header-bottom__search {
  display: flex;
  align-items: center;
}

.search {
  position: relative;
  display: flex;
  flex-grow: 1;
  padding: 0 15px;
}

.search__input {
  padding: 10px 14px;
  border-radius: 100px;
  background-color: #ffffff;
  width: 100%;
}

.search__btn {
  position: absolute;
  right: 10%;
  top: 25%;
  font-size: 16px;
  color: #005780;
}

@media (min-width: 768px) {
  .search__btn {
    right: 4%;
  }
}

html * {
  box-sizing: border-box;
}

html *::selection {
  background-color: #005780;
  color: #ffffff;
}

body,
html {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

#wrapper {
  position: relative;
  overflow: hidden;
}

main {
  overflow-y: auto;
  overflow-x: hidden;
}

body {
  min-width: 320px;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #464646;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0 0 10px;
}

img {
  max-width: 100%;
}

p {
  margin: 0 0 10px;
}

ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}

address {
  font-style: normal;
}

input {
  border: 0;
}

input[type="text"], input[type="password"], input[type="email"], input[type="search"] {
  -webkit-appearance: none;
}

button {
  border: 0;
  padding: 0;
  background-color: unset;
  cursor: pointer;
}

textarea {
  resize: vertical;
  -webkit-appearance: none;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.col-3 {
  display: flex;
  width: 60px;
}

@media (min-width: 1024px) {
  .col-3 {
    width: calc(100% / 3);
  }
}

@media (min-width: 1200px) {
  .col-3 {
    width: 25%;
  }
}

.col-4 {
  width: 100%;
}

@media (min-width: 1200px) {
  .col-4 {
    width: calc(100% / 3);
  }
}

.col-6 {
  width: 50%;
}

.col-9 {
  width: 100%;
}

@media (min-width: 1024px) {
  .col-9 {
    width: 66.6%;
  }
}

@media (min-width: 1200px) {
  .col-9 {
    width: 75%;
  }
}

.col-9.col-9-xs {
  width: 80%;
}

@media (min-width: 768px) {
  .col-9.col-9-xs {
    width: 92%;
  }
}

@media (min-width: 1024px) {
  .col-9.col-9-xs {
    width: 66.6%;
  }
}

@media (min-width: 1200px) {
  .col-9.col-9-xs {
    width: 75%;
  }
}

@media (min-width: 1200px) {
  .col-12 {
    width: 100%;
  }
}

.sidebar {
  position: fixed;
  top: 0;
  bottom: 0;
  left: -100%;
}

@media (min-width: 1024px) {
  .sidebar {
    position: relative;
    left: 0;
    background-color: #f9f9f9;
    width: 100%;
  }
}

.main-page {
  padding: 30px 15px 24px;
}

.main-title {
  color: #4d4d56;
  font-size: 28px;
  line-height: 34px;
  font-weight: 600;
  margin-bottom: 16px;
}

.secondary-title {
  color: #005780;
  font-size: 24px;
  line-height: 28px;
  font-weight: 600;
  margin-bottom: 18px;
  text-align: center;
}

.title-arrow {
  position: relative;
}

.title-arrow__left {
  position: absolute;
  left: 0%;
}

.title-arrow__right {
  position: absolute;
  right: 0%;
}

.alert {
  background-color: #d7f3ff;
  padding: 10px 14px;
  color: #005780;
  text-align: center;
  margin-bottom: 26px;
}

.box-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 25px;
}

.box-section__one {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.box-section__two {
  display: none;
}

@media (min-width: 1200px) {
  .box-section__two {
    display: flex;
    margin: 0 -8px;
  }
  .box-section__two li {
    padding: 0 8px;
  }
  .box-section__two a {
    color: #005780;
    font-size: 28px;
  }
  .box-section__two a:hover {
    color: #e9445df6;
  }
}

.item {
  background-color: #f2f2f2;
  border-radius: 23px;
  padding: 10px 14px;
  display: flex;
  align-items: center;
  color: #656565;
  text-align: center;
}

.span-box {
  margin-right: 10px;
}

.span-box-left {
  margin-left: 10px;
}

.d-flex {
  display: flex;
  padding: 0 15px;
  margin-bottom: 22px;
}

.d-flex.d-flex--sm {
  padding: 0 15px;
  margin-bottom: 14px;
  width: 100%;
}

.d-flex.d-flex--xs {
  margin-bottom: 0;
}

.btn-item {
  margin-right: 16px;
}

.navigation {
  display: none;
}

@media (min-width: 1200px) {
  .navigation {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 4px;
    margin-bottom: 28px;
  }
}

.select-box {
  padding: 15px;
  color: #656565;
  cursor: pointer;
}

.select-box__item {
  min-width: 270px;
  background-color: #ffffff;
  border: 1px solid #e5e5e5;
  border-radius: 20px;
  min-height: 40px;
  margin-bottom: 15px;
  padding: 10px 20px;
}

.select-box__item i {
  font-weight: 900;
}

.select-box__itemgroup {
  display: flex;
  flex-direction: column;
}

.select-box__itemgroup label {
  padding: 10px 0;
  width: 100px;
  cursor: pointer;
}

.select-box__itemgroup input {
  margin-right: 10px;
}

.title-box {
  padding-left: 8px;
}

.hidden {
  display: none;
}

.checkbox-btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  border: 1px solid #c4c4c4;
  border-radius: 5px;
  background-color: #fff;
}

.checkbox-btn i {
  font-size: 13px;
  opacity: 0;
  transition: all 0.3s ease;
  transform: rotate(-45deg);
}

.checkbox-text {
  display: inline-block;
  padding-left: 10px;
}

.checkbox input:checked + .checkbox-btn i {
  opacity: 1;
  color: #e9445df6;
}
